<script setup>
import {reactive} from "vue";
import {LikeOutlined} from '@vicons/antd'
import {homeGetResource} from "@/assets/js/api";
import {useMessage} from "naive-ui";

const message = useMessage()
const data = reactive({
  loading: false,
  resourceData: []
})

const queryData = reactive({
  kwd: '',
  order: 'desc',
  orderby: 'update_time',
})
const getResourceData = () => {
  data.loading = true
  homeGetResource(queryData).then(res => {
    if (res.data.code === 200) {
      data.resourceData = res.data.data
      data.loading = false
    } else if (res.data.code === 400) {
      message.error(res.data.msg)
      data.loading = false
    } else if (res.data.code === 401) {
      message.warning(res.data.msg)
      data.loading = false
    }
  }).catch(err => {
    message.error(err.message)
    data.loading = false
  })
}
getResourceData()
</script>

<template>
  <n-card>
    <div class="recommend-title">
      <n-icon :component="LikeOutlined" color="#18a058" size="19"/>
      <n-text type="primary" class="recommend-title-text">推荐</n-text>
    </div>
    <div class="recommend-content">
      <div v-if="!data.loading" v-for="item in data.resourceData.slice(0, 5)" class="recommend-content-item">
        <div class="item-img">
          <img :alt="item.cover" :src="item.cover">
        </div>
        <div class="item-info">
          <div class="item-info-title">
            <a :href="'/#/resource?resourceId=' + item.id">
              <n-gradient-text :size="13" type="primary">{{ item.name }}</n-gradient-text>
            </a>
          </div>
          <div class="item-info-tag">
            <n-tag size="small" style="margin-right: 5px" type="error">{{ item.category.name }}</n-tag>
            <n-tag v-for="tag in item.tags" :key="tag.id" size="small" style="margin-right: 5px" type="info">
              {{ tag.name }}
            </n-tag>
          </div>
        </div>
      </div>
      <div v-else v-for="i in 5" class="recommend-content-item">
        <div class="item-img">
          <n-skeleton :loading="data.loading" style="width: 100%;height: 100%" />
        </div>
        <div class="item-info">
          <n-skeleton text :loading="data.loading" :repeat="5" />
        </div>
      </div>
    </div>
  </n-card>
</template>

<style scoped>
.recommend-title {
  display: flex;
  align-items: center;
}

.recommend-title-text {
  margin-left: 5px;
  font-size: 14px;
}

.recommend-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.recommend-content-item {
  width: 100%;
  height: 65px;
  margin-bottom: 10px;
  display: flex;
  border: 0.5px solid #313131;
}

.item-img {
  width: 45%;
  height: 65px;
}

.item-img img {
  width: 100%;
  height: 100%;
}

.item-info {
  width: 55%;
  height: 65px;
  margin: 0 10px;
  overflow: hidden;
}

.item-info-title {
  width: 100%;
  margin-top: 8px;
  display: flex;
  align-items: center;
}

.item-info-title a {
  font-size: 14px;
  color: #ccc;
  text-decoration: none;
}

.item-info-tag {
  margin-top: 5px;
  display: flex;
  align-items: center;
}
</style>