<script setup>
import {reactive} from 'vue'
import {ArrowUpOutlined, HomeOutlined, MenuOutlined, UserOutlined} from "@vicons/antd";
import {ArrowBidirectionalUpDown24Filled} from "@vicons/fluent";
import {useRouter} from 'vue-router'
import SiderRecommend from "@/components/SiderRecommend.vue";
import {useStore} from 'vuex'
import {configCheck} from "@/assets/js/api";

const store = useStore()
const router = useRouter()
const scrollUp = () => {
  window.scrollTo({
    top: 0,
    behavior: 'smooth'
  })
}
const data = reactive({
  hideSidebar: store.state.hideSidebar,
})


</script>

<template>
  <n-layout class="main" embedded>
    <n-scrollbar>
      <router-view/>
      <n-float-button :bottom="24" :right="24" menu-trigger="hover">
        <n-icon :component="MenuOutlined" size="24" @click="scrollUp"/>
        <template #menu>
          <n-float-button shape="circle">
            <n-tooltip placement="left" trigger="hover">
              <template #trigger>
                <n-icon :component="UserOutlined" size="24" @click="router.push({name: 'adminHome'})"/>
              </template>
              个人中心
            </n-tooltip>
          </n-float-button>
          <n-float-button shape="circle">
            <n-tooltip placement="left" trigger="hover">
              <template #trigger>
                <n-icon :component="HomeOutlined" size="24" @click="router.push({name: 'home'})"/>
              </template>
              首页
            </n-tooltip>
          </n-float-button>
          <n-float-button class="float-button-hidden-sider" shape="circle"
                          @click="store.commit('setHideSidebar', !data.hideSidebar);data.hideSidebar = !data.hideSidebar">
            <n-tooltip placement="left" trigger="hover">
              <template #trigger>
                <n-icon :component="ArrowBidirectionalUpDown24Filled" size="24" style="transform: rotate(90deg)"/>
              </template>
              {{ data.hideSidebar ? '隐藏侧边栏' : '显示侧边栏' }}
            </n-tooltip>
          </n-float-button>
          <n-float-button shape="circle">
            <n-tooltip placement="left" trigger="hover">
              <template #trigger>
                <n-icon :component="ArrowUpOutlined" size="24" @click="scrollUp"/>
              </template>
              回到顶部
            </n-tooltip>
          </n-float-button>
        </template>
      </n-float-button>
      <SiderRecommend v-if="data.hideSidebar" class="sider-recommend"/>
    </n-scrollbar>
  </n-layout>
</template>

<style scoped>
.sider-recommend {
  position: fixed;
  left: 15px;
  top: 110px;
  width: 18.2%;
}

.main {
  padding: 35px;
  margin: 0 auto;
  width: 65%;
  transition: all .5s;
}

@media screen and (max-width: 1200px) {
  .main {
    width: 80%;
    padding: 0;
  }

  .sider-recommend {
    display: none;
  }

  .float-button-hidden-sider {
    display: none;
  }
}

@media screen and (max-width: 800px) {
  .main {
    width: 90%;
  }
}

@media screen and (max-width: 600px) {
  .main {
    width: 100%;
  }
}
</style>